import React, { useEffect, useState } from 'react';
import Iframe from 'react-iframe';
import data from '../core/data';

const PageBlogpost = () => {
    const [isData, setisData] = useState(false);
    useEffect(() => {
        const decryptQuery = data.parseQuery();
        const cid = (decryptQuery && decryptQuery.cid) ? decryptQuery.cid : '';
        const tpid = (decryptQuery && decryptQuery.tpid) ? decryptQuery.tpid : '';
        data.getTopicJSON(cid, tpid).then((result: any) => {
            data.topicJSON = result.data;
            setisData(true);
        });
    }, []);

    return (
        <div>
            {isData && <PlayerMain />}
        </div>
    );
};

const PlayerMain = () => {
    return (
        <div>
            <div style={{ width: "2em", height: "0.3em", backgroundColor: "#000", marginTop: "2.7rem" }}></div>
            <h2 style={{ borderBottom: "1px solid #999", marginTop: "0.2em", paddingBottom: "0.2em" }}>한글을 보고 영어로 말해보세요.</h2>
            <PlayerLists lang="ko" />
            <p>&nbsp;</p>
            <div style={{ width: "2em", height: "0.3em", backgroundColor: "#000", marginTop: "2.7rem" }}></div>
            <h2 style={{ borderBottom: "1px solid #999", marginTop: "0.2em", paddingBottom: "0.2em", marginBottom: "0" }}>한글</h2>
            <Iframe url={"http://localhost:9823/minip" + window.location.search}
                height={String(30 + (29 * data.topicJSON.sentense.length))}
                width="100%"
                position="relative"
                frameBorder={0}
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            />
            <p>&nbsp;</p>
            <div style={{ width: "2em", height: "0.3em", backgroundColor: "#000", marginTop: "2.7rem" }}></div>
            <h2 style={{ borderBottom: "1px solid #999", marginTop: "0.2em", paddingBottom: "0.2em" }}>한글</h2>
            <PlayerLists lang="en" />
            <p>&nbsp;</p>
        </div>
    );
}

const PlayerLists = ({ lang }: { lang: string }) => {
    const SentenseList = data.topicJSON.sentense.map((item: any, i: number) => {
        return <Sentense info={item} key={i} idx={i} lang={lang} />
    })
    return (<ul style={{ paddingLeft: "20px" }}>
        {SentenseList}
    </ul>);
}

const Sentense = ({
    info, idx, lang
}: {
    info: any, idx: number, lang: string
}) => {
    const words = info.words;
    return (
        <>
            <li style={{ display: "flex" }}>
                <div style={{ marginRight: "5px" }}>{idx + 1}.</div>
                <div>{info[lang]}</div>
            </li>
            {lang === 'en' && words && <WordList json={words} />}
        </>
    );
}

const WordList = ({
    json
}: {
    json: any
}) => {
    const Itm = json.map((item: string[], i: number) => {
        return <li>
            <span><i>* {item[0]}</i></span>
            <span> : </span>
            <span><i>{item[1]}</i></span>
        </li>
    })
    return (
        <ul style={{ fontSize: "0.9em", marginLeft: "17px", marginBottom: "10px" }}>
            {Itm}
        </ul>
    );
}

export default PageBlogpost;